* html, body {
  margin: 0;
  padding: 0;
  font-family: Open Sans;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 15px;
}

.home-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.board {
  margin: 1em auto 1em auto;
  width: 50vh;
  height: 50vh;
  border: 1px solid black;
  padding: 4vh;
  z-index: 0;
}

.sidebar-image-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
}

.sidebar-image {
  width: 7em;
  height: 7em;
  margin: 0.5em;
  padding: 0;
  position: absolute;
}

.side-bar {
  width: 53vh;
  margin: 1em;
  display: -ms-flexbox;
  display: flex;
  overflow-x: auto;
}

.creation-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

@media (min-width: 625px) {
  .board {
    margin: 1em auto 1em auto;
    width: 70vh;
    height: 70vh;
    padding: 6vh;
  }
  .side-bar {
    width: 73vh;
  }
}

@media (min-width: 768px) {
  .side-bar {
    width: 82vh;
  }
  .board {
    width: 80vh;
    height: 80vh;
    padding: 6vh;
  }
}

@media (min-width: 1024px) {
  .creation-main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }
  .sidebar-image {
    margin: 0;
    padding: 0;
  }
  .side-bar {
    width: 60vh;
    margin: 1em 1em;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: 1em;
  }
}




